.services{
    background-color: #e7e7e7;
    padding: 100px 0px 70px;
}
.services .sectionTitle h1::before{
    left: 30%;
}
.services .content .row{
    flex: 0 0 100%;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.services .content .box{
    flex: 0 0 calc(33.33% - 20px);
    max-width: calc(33.33% - 20px);
    border: 1px solid #dbdbdb;
    padding: 30px;
    text-align: center;
    background-color: #ffffff;
    position: relative;
}
.services .content .box::before{
    content: '';
    height: 2px;
    position: absolute;
    background-color: #febd01;
    bottom: 0px;
    width: 60px;
    left: 0;
    right: 0;
    margin: auto;
    transition: all .5s ease;
}
.services .content .box:hover::before{
    width: 100%;
}
.services .content .box .icon{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 0 auto 20px;
    background-color: #febd01;
}
.services .content .box .icon .fab , .services .content .box .icon i{
    font-size: 25px;
    color: #ffffff;
    line-height: 60px;
}
.services .content .box h5{
    font-size: 16px;
    font-weight: 600;
    color: #000000;
    margin: 0;
}
.services .content .box p{
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: #838383;
    margin: 10px 0 0;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.sectionTitle{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 40px;
}
.sectionTitle h1{
    display: inline-block;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 700;
    color: #000000;
    margin: 0 0 5px;
    position: relative;
    padding-bottom: 5px;
}
.sectionTitle h1::before{
    content: '';
    left: 0;
    position: absolute;
    background-color: #febd01;
    height: 2px;
    right: 30%;
    bottom: 0px;
}
.textCenter{
    text-align: center!important;
}
@media(max-width: 767px){
    .services .content{
        padding-left: 20px;
        padding-right: 20px;
    }
    .services .content .box{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 30px;
    }
}