.home{
    min-height: 100vh;
    background-image:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url('../images/home.jpg');
    background-size: cover;
    background-position: center;
    padding: 15px;
}
.home .fullScreen{
    min-height: 100vh;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.home .homeContent{
    flex: 0 0 100%;
    max-width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.home .homeContent .block{
    flex: 0 0 75%;
    max-width: 75%;
    padding-left: 35px;
    border-left: 5px solid #ffffff;
    margin-top: 50px;
}
.home .homeContent .block h6{
    color: #ffffff;
    font-size: 18px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    line-height: 1.2;
}
.home .homeContent .block h1{
    color: #ffffff;
    font-size: 60px;
    font-weight: 600;
    margin: 0;
    padding: 0;
    line-height: 1.2;
}
.home .homeContent .block h3{
    color: #ffffff;
    font-size: 25px;
    font-weight: 500;
    margin: 0;
    padding: 0;
    line-height: 1.2;
}
.home .homeContent .block .cvBtn{
    padding-top: 20px;
}
.home .homeContent .block .cvBtn button{
    border-radius: 4px;
    color: #ffffff;
    border: 1px solid #ffffff;
    background-color: transparent;
    padding: 12px 25px;
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    cursor: pointer;
    transition: all .5s ease;
}
.home .homeContent .block .cvBtn button:hover{
    background-color: #ffffff;
    color: #000000;
}
@media(max-width: 991px){
    .home .homeContent .block h1{
        font-size: 50px;
    }
}
@media(max-width: 767px){
    .home .homeContent .block h1{
        font-size: 40px;
    }
    .home .homeContent .block h3{
        font-size: 25px;
    }
}
@media(max-width: 500px){
    .home .homeContent .block h1{
        font-size: 30px;
    }
    .home .homeContent .block h3{
        font-size: 20px;
    }
    .home .homeContent .block{
        padding-left: 20px;
    }
}