.header{
    background-color: #f9f9f9;
    position: fixed;
    border-bottom: 1px solid #c3c3c3;
    z-index: 1;
    padding: 0px 15px;
    width: 100%;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.header .row{
    justify-content: space-between;
    align-items: center;
}
.header .brand a{
    text-decoration: none;
    font-size: 22px;
    font-weight: 600;
    color: #000000;
    text-transform: uppercase;
}
.header .navbar ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
}
.header .navbar ul li{
    display: inline-block;
    margin-left: 35px;
}
.header .navbar ul li a{
    font-size: 15px;
    text-decoration: none;
    line-height: 72px;
    display: block;
    color: #000000;
    font-weight: 600;
    position: relative;
    cursor: pointer;
}
.header .navbar ul li a::before{
    content: '';
    height: 3px;
    background-color: #ffffff;
    width: 0%;
    position: absolute;
    right: 0;
    bottom: -1px;
    transition: all .5s ease;
}
.header .navbar ul li a::before{
    background-color: #febd01;
}
.header .navbar ul li a:hover::before{
    width: 100%;
    left: 0;
}
.header .navbar ul li a.active::before{
    width: 100%;
    left: 0;
}
.header .navbar .active li a::before{
    width: 100%;
    left: 0;
}
.hamBurger i{
    font-size: 20px;
    display: none;
    height: 30px;
    width: 35px;
    background-color: #ffffff;
    color: #000000;
    border-radius: 3px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    border: 1px solid #eeeeee;
}  

@media(max-width: 767px){
    .header{
        padding: 15px;
        position: fixed;
        background-color: #f9f9f9;
        border: none;
        align-items: center;
        z-index: 11;
    }
    .navbar{
        position: absolute;
        background-color: #f9f9f9;
        width: 100%;
        left: 0;
        right: 0;
        top: 62px;
        padding: 15px;
        border-top: 1px solid #eeeeee;
        border-bottom: 1px solid #eeeeee;
        z-index: 1;
        display: none;
    }
    .navbar.show{
        display: inline-block;
        animation: fadeIn 1s ease;
    }
    .hamBurger i{
        display: block;
    }
    .header .navbar ul li{
        display: block;
        margin-left: 0;
    }
    .header .navbar ul li a{
        line-height: 42px;
        color: #000000;
        text-decoration: none;
    }
    .header .navbar ul li a::before{
        background-color: #febd01;
    }

}

@keyframes fadeIn {
    0% {
        opacity: 0;
        transform: translateY(startYposition);
    } 
    100% {
        opacity: 1;
        transform: translateY(endYposition);
    }
}
