.aboutme{
    padding: 100px 15px;
    background-color: #ffffff;
}
.sectionTitle{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 40px;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.sectionTitle h1{
    display: inline-block;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 700;
    color: #000000;
    margin: 0 0 5px;
    position: relative;
    padding-bottom: 5px;
}
.sectionTitle h1::before{
    content: '';
    left: 0;
    position: absolute;
    background-color: #febd01;
    height: 2px;
    right: 30%;
    bottom: 0px;
}
.uppercase {
    text-transform: uppercase!important;
}
.aboutme .sectionTitle p.small{
    display: block;
    font-size: 14px;
    color: #838383;
    font-weight: 400;
    letter-spacing: 2px;
}
.aboutme .aboutContent .image{
    flex: 0 0 33.33%;
    max-width: 33.33%;
}
.aboutme .aboutContent .image img{
    width: 100%;
    display: block;
}
.aboutme .aboutContent .text{
    flex: 0 0 66.66%;
    max-width: 66.66%;
    padding-left: 30px;
}
.aboutme .aboutContent .text h4{
    font-size: 25px;
    font-weight: 600;
    margin: 0 0 5px;
}
.aboutme .aboutContent .text h6{
    font-size: 18px;
    font-weight: 500;
    margin: 0 0 15px;
    color: #000000;
}
.aboutme .aboutContent .text p{
    font-size: 15px;
    font-weight: 400;
    color: #838383;
    line-height: 24px;
    margin: 0 0 20px;
}
.aboutme .aboutContent .text h6 span{
    color: #febd01;
}
.aboutme .aboutContent .text .info{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}
.aboutme .aboutContent .text .info .list{
    flex: 0 0 calc(50% - 15px);
    max-width: calc(50% - 15px);
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #dee2e6;
    padding-bottom: 10px;
}
.aboutme .aboutContent .text .info .list label{
    padding-right: 10px;
    color: #000000;
    font-weight: 600;
    font-size: 15px;
}
.aboutme .aboutContent .text .info .list p{
    margin: 0;
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: #838383;

}
.aboutme .aboutContent .text .socialLinks a{
    height: 30px;
    width: 30px;
    background-color: #000000;
    border-radius: 3px;
    display: inline-block;
    border: 1px solid #000000;
    line-height: 28px;
    text-align: center;
    margin: 15px 1px;
}
.aboutme .aboutContent .text .socialLinks a i{
    color: #ffffff;
    font-size: 12px;
}
.aboutme .aboutContent .text .socialLinks a:hover{
    background-color: #43435f;
}
@media(max-width: 767px){
    .aboutme .aboutContent .text{
        flex: 0 0 100%;
        max-width: 100%;
        padding-left: 0;
        padding-top: 30px;
    }
    .aboutme .aboutContent .image{
        flex: 0 0 100%;
        max-width: 100%;
    }
}
@media(max-width: 500px){
    .aboutme .aboutContent .text .info .list p{
        margin: 0;
        flex: 0 0 100%;
    }
}