/* skills section */
.m{
    padding-right: 20px;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.sectionTitle{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 40px;
}
.sectionTitle h1{
    display: inline-block;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 700;
    color: #000000;
    margin: 0 0 5px;
    position: relative;
    padding-bottom: 5px;
}
.sectionTitle p{
    display: block;
    font-size: 14px;
    color: #838383;
    font-weight: 400;
    letter-spacing: 2px;
}
.sectionTitle h1::before{
    content: '';
    left: 0;
    position: absolute;
    background-color: #febd01;
    height: 2px;
    right: 30%;
    bottom: 0px;
}
.skills{
    padding: 100px 15px;
    background-color: #ffffff;
}
.alignItemCenter{
    align-items: center;
}
.skills .skillsContent{
    flex: 0 0 50%;
    max-width: 50%;
}
.skills .skillsContent .skillBox{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 15px;
}
.skills .skillsContent .skillBox .skillBar{
    height: 8px;
    background-color: #eeeeee;
    position: relative;
}
.skills .skillsContent .skillBox .skillBar .skillBarIn{
    background-color: #febd01;
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
}
.skills .skillsContent .skillBox .skillBar .skillBarIn span{
    color: #838383;
    font-size: 16px;
    font-weight: 400;
    position: absolute;
    right: 0;
    top: -30px;
}
.img{
    margin-left: 5rem;
}
/* Table section */
.table{
    background-color: #f9f9f9;
    padding: 50px;
}
.table .container{
    text-align: center;
}
.table .container .sectionTitle h1::before{
    left: 30%;
    right: 30%;
}
.tableContent{
    border-collapse: collapse;
    border-spacing: 0;
    overflow: hidden;
    border-radius: 10px 10px 0 0;
    margin: 25px 0;
    font-size: 18px;
    min-width: 400px;
    text-align: center;
    display: inline-block;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.8);
}
.tableContent thead th{
    background-color: #febd01;
    color: #ffffff;
    text-align: left;
    font-weight: 600;
    text-align: center;
}
.tableContent th ,.tableContent td{
    padding: 12px 15px;
}
.tableContent tbody tr:nth-child(odd){
    background-color: #ffffff;
}
.tableContent tbody tr:nth-child(even){
    background-color: #ededed;
}

@media(max-width: 991px){
    .skills .skillsContent{
        flex: 0 0 100%;
        max-width: 100%;
    }
    .img{
        width: 100%;
        margin: 0;
        text-align: center;
    }
    .img img{
        display: inline-block;
    }
}
@media(max-width: 500px){
    .tableContent tbody tr td{
        font-size: 15px;
    }

}