.contact{
    padding: 100px 50px;
    background: #7F00FF;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #E100FF, #7F00FF);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #E100FF, #7F00FF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.sectionTitle{
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 40px;
}
.sectionTitle h1{
    display: inline-block;
    font-size: 35px;
    text-transform: uppercase;
    font-weight: 700;
    color: #ffffff;
    margin: 0 0 5px;
    position: relative;
    padding-bottom: 5px;
}
.sectionTitle h1::before{
    content: '';
    left: 0;
    position: absolute;
    background-color: #febd01;
    height: 2px;
    right: 30%;
    bottom: 0px;
}
.textCenter{
    text-align: center!important;
}
.container{
    max-width: 1140px;
    margin: auto;
}
.row{
    display: flex;
    flex-wrap: wrap;
}
.contact .form{
    background-color: #ffffff;
    flex: 0 0 100%;
    max-width: 100%;
    padding: 40px;
    border: 1px solid #eeeeee;

}
.contact .form .text{
    flex: 0 0 100%;
    max-width: 100%;
    padding-bottom: 20px;
}
.contact .form .text h2{
    font-size: 25px;
    font-weight: 600;
    color: #000000;
    margin: 0;
    padding: 0;
}
.contact .form .text p{
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    color: #838383;
    margin: 0 0 10px;
}
.contact .form .space_between{
    justify-content: space-between;
}
.contact .form .col_6{
    flex: 0 0 calc(50% - 15px);
    max-width: calc(50% - 15px);
}
.contact .form .col_12{
    flex: 0 0 100%;
    max-width: 100%;
}
.contact .form .form_control{
    height: 45px;
    width: 100%;
    margin-bottom: 15px;
    border: 1px solid #ced4da;
    padding: 6px 12px;
    font-size: 14px;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
}
.contact .form textarea.form_control{
    height: 140px;
}
.contact .form .form_control::placeholder{
    color: #838383;
}
.contact .form .btn{
    flex: 0 0 100%;
    max-width: 100%;
}
.align_right{
    text-align: right;
}
.contact .form .btn button{
    border-radius: 4px;
    color: #ffffff;
    border: 1px solid #ffffff;
    padding: 12px 25px;
    display: inline-block;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    transition: all .5s ease;
    background-color: #febd01;
    cursor: pointer;
}
.contact .form .btn button:hover{
    background-color: #000000;
    color: #ffffff;
}